<template>
    <div id="main">
        <img src="./pexels-josh-sorenson-976873.jpg" id="bc1">
        <ul style="margin-top: 150px; z-index: 10;" >
            <li style="border-color: paleturquoise;" @click="togame" >
               <span>游戏介绍</span>  <img src='./two_pingpong.png'>
            </li>
            <li style="border-color:aqua;" @click="togame1" >
                <span>积分模式</span> <img src='./bat.png'>
            </li >
            <li style="border-color: plum;" @click="togame2" >
                <span>对抗模式</span>  <img src='./bat2.png'>
            </li>
            <li style="border-color: purple;" @click="togame3" >
                <span>自由模式</span>  <img src='./two_pingpong.png'>
            </li>
        </ul>
        <div id="poster">
            <div id="title" style="position: relative; z-index: 10;">
                <h1>乒乓高手<img src="./assets/burn.jpg" style="width: 100px; height: 100px;display: inline-block;"></h1>
            </div>
            
            <div id="carousel"> 
                <p style="border-radius: 2px;">激情比赛</p>
                <img :src="matchs[nowdisplay]" style="border-radius: 5px;">
            </div>
            
        </div>
        
    </div>
</template>

<script>
import { onMounted, reactive, ref } from 'vue'
export default {

    name:'menus',
    emits:['startgame'],
    props:{
        loggedin:Boolean
    },
    setup(props,context) {
        onMounted(()=>{
            setInterval(() => {
                nowdisplay.value=(nowdisplay.value+1)%3;
            }, 2500);
        })
        
        let nowdisplay=ref(0);
        let matchs=reactive([require('@/assets/match1.jpg'),require('@/assets/match2.webp'),require('@/assets/match3.jpg')] )
         function togame(event){
            context.emit('startgame',0)
         }
         function togame1(event){
            context.emit('startgame',1)
         }
         function togame2(event){
            context.emit('startgame',2)
         }
         function togame3(event){
            context.emit('startgame',3)
         }
        return{matchs,nowdisplay,togame,togame1,togame2,togame3}
    },
}
</script>
<style scoped>
ul{
    position: relative;
    list-style: none;
    float: left;
}
li{
    width: 450px;
    height: 100px;
    margin: 0px 50px 15px 100px;
    font-size: 55px;
    color:blue;
    text-align: center;
    font-family:'Courier New', Courier, monospace;
    font-weight: bold;
    text-shadow: 3px 4px 3px aliceblue;
    transition:all 1s linear;
    border-left: 20px solid;
    z-index: 10;
}
li:hover{
margin-left: 150px;
}
li img{
    display: inline-block;
    height: 80%;
    width: auto;

}
li span{
    line-height: 100px;
}
#bc1{
    width: 100vw;
    height:100vh ;
    position: absolute;
    left: 0%;
    top: 0%;
    z-index:  0;
}
#main{
    width: 100vw;
    height: 100vh;
    background-color: cornflowerblue;
    position: absolute;
    top: 0;
    
}
#poster{
    float: right;
    width: 40%;
    height: 100%;

}
h1{
    font-size: 75px;
    font-family: 'Times New Roman', Times, serif;
    color: aliceblue;
}
#carousel img{
    width: 80%;
    height:auto;
    margin: auto;
}
#carousel p{
    background-color: white;
    width: 100px;
    left: 10%;
    top: -10%;
    position: absolute;
}
#carousel{
    position: relative;
}
</style>