<template>
 <menus :loggedin="islogged" v-if="now==0" @startgame="game_load"></menus>
 <t1 cwidth="900" cheight="900" :gametype="gametype" v-if="now==1"></t1>
 <description v-if="now==2"></description>

</template>

<script>
import { onMounted, reactive, ref } from 'vue'
import t1 from './t1.vue';
import menus from './menus.vue'
import description from './description.vue';
import Description from './description.vue';
export default {
  name: 'App',
  components: {
    t1,menus,
    Description
  },setup(){
    
    let isgaming=ref(false);
    let gametype=ref(0);
    let now=ref(0);
    let islogged=ref(false);
    function game_load(s){
      if(s==0)
      {
        now.value=2;
      }
      else
      {
        now.value=1;
        gametype.value=s-1;
      }
    }

    return{
      isgaming,gametype,now,islogged,game_load
    }
  }
}
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;

}

</style>
