<template>
     <audio class="audio"
           controls
           ref="s_bat"
           hidden="true"
          >
    </audio>
    <audio class="audio"
           controls    
           ref="bounce"
           hidden="true"
          >
    </audio>
    <audio class="audio"
           controls   
           ref="cheer"
           hidden="true"
          >
    </audio>
    <audio class="audio"
           controls   
           ref="down"
           hidden="true"
          >
    </audio>
    <div id="gameconsole" @mousemove="moves" @click="fires" @keydown="fires" v-if="!showhs" >
        <img src="./table.jpg" style="width: 80vw; height: 100vh; position: absolute; left: 10%; z-index: 0;">
        <div id="scoresboard" v-if="gametype==0"><p>游戏分数：{{ scores }}</p>
        <p>连击次数*<b>{{ keep }}</b></p>
        <button @click="subitscore">提交分数</button>
        </div>
        <div id="pointscore" v-if="gametype==1">{{ me_score }} VS {{ ai_score }}</div>
        <div id="mybat" :style="{
            left:pointx+'px',top:pointy+'px',
            'transform':'rotate('+rotatesz+'deg)'+'rotateX('+rotatesx+'deg)'

        }">
        <img src="./bat.png" style="width: 100%; height: 100%;">
        </div>
        <div id="pingpong" :style="{
            left:ballx+'px',top:ball_vy+'px'
        }" v-show="showpingpong">
        <img src="./pingpong.png" style="width: 100%; height: 100%;">
    </div>
          <div id="enemybat" :style="{
            left:ai_x+'px',top:ai_y+'px',
            'transform':'rotate('+rotatesz_ai+'deg)'+'rotateX('+rotatesx_ai+'deg)'

        }">
        <img src="./bat.png" style="width: 100%; height: 100%;">
    </div>



</div>
<div id="scoreboard" v-if="showhs">
        <h3>游戏高分榜</h3>
        <textarea id="highscore" readonly></textarea>

        <p>提交你的成绩: 当前成绩:{{ scores }}</p>
        <input type="text" id="scorename" placeholder="nameless">
        <button @click="truesubmit">立即提交</button>
    </div>
</template>
<script>
import axios from 'axios'
import { computed, onMounted, ref } from 'vue';
export default {
    name:'t1',
    props:{
        'cwidth':String,
        'cheight':String,
        'gametype':Number
    },
    setup(props,context)
 {      let keepPressL=0;
        let keepPressR=0;
        let me_score=ref(0);
        let ai_score=ref(0);
        let gametype=props.gametype;
        let keep=ref(0);
        let scores=ref(0);
        let caught=0;
        let setplace=0;
        let hit=0;
        let ai_x=ref(0);
        let ai_y=ref(0);
        let rotatesz_ai=ref(0);
        let rotatesx_ai=ref(0);
        let showpingpong=ref(true);
        let pingpongstate=0;
        let down_count=0;
        let vx_pingpong=0;
        let vy_pingpong=0;
        let fall=5;
        let pointx=ref(0);
        let pointy=ref(0);
        let rotatesz=ref(0);
        let rotatesx=ref(0);
        let ballx=ref(600);
        let bally=ref(600);
        let ballz=ref(600);
        let vx=ref(0);
        let vy=ref(0);
        let tmpx=0;
        let tmpy=0;
        let ttmpx=0;
        let ttpmy=0;
        let ball_vy=computed(()=>{
            return bally.value/1.414+ballz.value/1.414;
        })
        let clienth=0;
        let clientw=0;
        let drop_y=0;
        let usingkey=false;
        let reached=false;
                  // 获取元素标签
    	let s_bat = ref(null);
       let bounce=ref(null);
        let cheer=ref(null);
        let down=ref(null);
        //算窗口大小
        onMounted(()=>{
            // console.log(s_bat.value)
            s_bat.value.src=require("./bat.mp3")
            bounce.value.src=require("./bounce.mp3")
            down.value.src=require("./down.mp3")
            cheer.value.src=require("./cheer.mp3")
            s_bat.value.pause();
            bounce.value.pause();
            down.value.pause();
            cheer.value.pause();
            clientw=window.innerWidth;
            clienth=window.innerHeight;
            // console.log(clientw,clienth);
        })
        //算拍子的速度
        onMounted(()=>{
            setInterval(() => {
                vx.value=9*(tmpx-ttmpx);
                vy.value=9*(tmpy-ttpmy);
                ttmpx=tmpx;
                ttpmy=tmpy;
                // console.log(vx.value,vy.value)
            }, 9);
        })

        //球的控制
        onMounted(()=>{
            setInterval(() => {
                //发球前
                if(pingpongstate==0)
                {
                    ballx.value=pointx.value+20;
                    bally.value=1.414*pointy.value;
                    ballz.value=0;
                    if(vy.value<-60 )
                    pingpongstate=1;
                    vx_pingpong=0;
                    vy_pingpong=0;
                    drop_y=clienth;
                    
                //初始发球时确定速度
                }if(pingpongstate==1)
                {
                    s_bat.value.play();
                    vx_pingpong=vx.value;
                    vy_pingpong=Math.max(-200,Math.min(-90,vy.value)) ;
                    pingpongstate=2;
                    reached=false;
                    // alert(vy_pingpong+'!')
                    // console.log(vx_pingpong,vy_pingpong)
                }
                //发向敌人
                if(pingpongstate==2)
                {   
                    if(ball_vy.value>1.3*clienth ||ball_vy.value<-clienth/3)
                    pingpongstate=7;
                    ballx.value+=vx_pingpong/42;
                    bally.value+=vy_pingpong/28;
                    if((ballx.value>((-ball_vy.value+685)/1.7 ) &&ballx.value<((ball_vy.value+1400)/1.32)))
                    {
                        if(fall>0)
                    {
                        ballz.value+=fall/5;
                        fall-=1;
                        if(fall==0)
                        {
                            fall=-35
                            bounce.value.play();
                        }
                        
                    }
                    if(fall<0)
                    {
                        ballz.value+=fall/5;
                        fall+=1;
                        if(fall==0)
                        fall=35;
                    }
                    }
                    else
                    {   
                   
                        drop_y=ball_vy.value;
                        pingpongstate=3; 
                        down_count=0;
                    }
                         

                       
                    // console.log(bally.value)
                }
                //坠地
                if(pingpongstate==3)
                {
                    ballx.value+=vx_pingpong/50;
                    if(fall>0)
                    {
                        ballz.value+=fall/5;
                        fall+=1;
                    
                    }
                    if(fall<=0)
                    {   
                        ballz.value+=fall/5;
                        fall+=7;
                    }
                    down_count++;
                    if(down_count>=45||fall>40)
                    {
                        pingpongstate=4;
                    }
                        
                }
                //坠地后复位
                if(pingpongstate==4)
                {        
                    if(showpingpong.value==true)
                    {
                        if(gametype==0)
                {   if(ball_vy.value>clienth/3)
                    {
                        down.value.play();
                        scores.value=Math.max(0,scores.value-15);
                        keep.value=0;
                    }
                    else
                    {
                        cheer.value.play();
                        scores.value=Math.max(0,scores.value+15);
                        keep.value=0;
                    }
                }
                if(gametype==1)
                {   if(ball_vy.value>clienth/3)
                    {
                        down.value.play();
                        ai_score.value++;
                    }
                    else
                    {
                        cheer.value.play();
                        me_score.value++;
                    }
                }
                    }
                    showpingpong.value=false;
                    setTimeout(() => {
                        pingpongstate=0;
                        showpingpong.value=true;
                
                    }, 800);
                }
                //被敌方击中时确定速度
                if(pingpongstate==5)
                {
                    s_bat.value.play();
                    if(vy_pingpong>-150)
                    {//速度低的时候敌人会抓住机会，试图发快远球
                        //必然加速，然后方向尽可能向最远方向发去，让对面接不到
                       
                        vy_pingpong=-vy_pingpong+(Math.random())*30+30;
                        vx_pingpong=0.3*vx_pingpong+0.6*(ai_x.value-0.5*clientw-150)+(Math.random()-0.5)*10;

                    }
                    else{
                        //让最快速度有个上限
                        if(vx_pingpong>100)
                        vx_pingpong=Math.min(200,vx_pingpong)+(Math.random()-0.5)*50;
                        else if(vx_pingpong<-100)
                        vx_pingpong=Math.max(-200,vx_pingpong)+(Math.random()-0.5)*50;
                        else
                        vx_pingpong=vx_pingpong-0.2*(ai_x.value-0.5*clientw-50)+(Math.random()-0.5)*10;
                        //让最快速度有个上限
                        vy_pingpong=Math.max(150,-vy_pingpong)*(0.7+Math.max(0,(Math.random()-0.7)))+10+Math.random()*50;
                    }
                    
                    pingpongstate=6;
                }
                //乒乓反弹
                if(pingpongstate==6)
                {
                    if(ball_vy.value>1.2*clienth ||ball_vy.value<-clienth/3)
                    pingpongstate=7;
                    ballx.value+=vx_pingpong/42;
                    bally.value+=vy_pingpong/28;
                    if((ballx.value>((-ball_vy.value+685)/1.7 ) &&ballx.value<((ball_vy.value+1400)/1.32)))
                    {
                        if(fall>0)
                    {
                        ballz.value+=fall/5;
                        fall-=1;
                        if(fall==0)
                        {
                            fall=-35;
                            bounce.value.play();
                        }
                        
                    }
                    if(fall<0)
                    {
                        ballz.value+=fall/5;
                        fall+=1;
                        if(fall==0)
                        fall=35;
                    }
                    }
                    else
                    {   
                      
                        drop_y=ball_vy.value;
                        pingpongstate=3; 
                        down_count=0;
                    }
                    if(pointx.value>ballx.value-150 &&pointx.value<ballx.value+20 &&pointy.value<ball_vy.value+65 &&pointy.value>ball_vy.value-130)
                    {

                        //接球的时候不仅要有接触还要有一定速度
                        //键盘模式下不用此方法
                        if((vy.value<-20 ||vx.value<-35||vx.value>35)&&!usingkey)
                        {

                         s_bat.value.play();
                        vx_pingpong=-vx_pingpong/3+ vx.value*1.6 ;
                        vy_pingpong=vy.value-vy_pingpong/2.5;
                        pingpongstate=2;
                        if(gametype==0)
                        {
                            
                            scores.value+=keep.value;
                            keep.value++;

                        }
                        }
                        

                    }
    
            }
            if(pingpongstate==7)
            {
                //此时说明球已经出了边界，需要重置状态
                
                pingpongstate=0;
                vx_pingpong=0;
                vy_pingpong=0;
                //积分模式下积分
                if(gametype==0)
                {   if(ball_vy.value>clienth)
                    {   down.value.play();
                        scores.value=Math.max(0,scores.value-15);
                        keep.value=0;
                    }
                    else
                    {
                        cheer.value.play();
                        scores.value=Math.max(0,scores.value+15);
                        keep.value=0;
                    }
                }    //对抗模式下比分
                    if(gametype==1)
                {   if(ball_vy.value>clienth)
                    {
                        down.value.play();
                        ai_score.value++;
                    }
                    else
                    {
                        cheer.value.play();
                        me_score.value++;
                    }
                } 

               
            }

            }, 10);
        })
        
        //球拍的移动(鼠标)
        function moves(event){
        // console.log(event);
        tmpx=event.clientX;
        tmpy=event.clientY;
        // console.log(tmpx,tmpy)
        pointx.value=event.clientX-75;
        if(event.clientY-75>=clienth/3)
            pointy.value=event.clientY-75;    
        else
           pointy.value=clienth/3+75; 
        
        rotatesz.value=-60+120*(event.clientX-75)/clientw;
        rotatesx.value=-30+(event.clientY-clienth/2)/clienth*90;
        }
        //球拍的移动(键盘按下策略)
        document.onkeydown=function(e){
            setTimeout(() => {
                if(e.code=='ArrowLeft')
            {   
                keepPressR=0;
                if(pointx.value>0)
                pointx.value-=20+3*keepPressL;
                tmpx=pointx;
                tmpy=pointy;
                rotatesz.value=-60+120*(pointx.value-75)/clientw;
                usingkey=true;
                keepPressL+=1;
                setTimeout(() => {
                    ()=>{
                        if(keepPressL>1)
                        keepPressL-=1;
                    }
                    
                }, 300);
            setTimeout(() => {
                usingkey=false;
            }, 2000);
            }
           
             if(e.code=='ArrowRight')
            {
                keepPressL=0;
                if(pointx.value<clientw)
                pointx.value+=20+3*keepPressR;
                tmpx=pointx;
                tmpy=pointy;
                rotatesz.value=-60+120*(pointx.value-75)/clientw;
                usingkey=true;
                keepPressR+=1;
                setTimeout(() => {
                    ()=>{
                        if(keepPressR>1)
                        keepPressR-=1;
                    }
                    
                }, 300);
            setTimeout(() => {
                usingkey=false;
            }, 2000);
            }


             if(e.code=='ArrowDown')
            {
                if(pointy.value<clienth)
                pointy.value+=20;
                tmpx=pointx;
                tmpy=pointy;
                rotatesx.value=-30+(pointy.value-clienth/2)/clienth*90;
                usingkey=true;
            setTimeout(() => {
                usingkey=false;
            }, 2000);
            }else if(e.code=='ArrowUp')
            {
                if(pointy.value>0)
                pointy.value-=20;
                tmpx=pointx;
                tmpy=pointy;
                rotatesx.value=-30+(pointy.value-clienth/2)/clienth*90;
                usingkey=true;
            setTimeout(() => {
                usingkey=false;
            }, 2000);
            }
            else if(e.code=='Space'){
                fires();
            }
            }, 1);
        }




        function fires(){
            if(pingpongstate==0)
            {
                pingpongstate=2;
            vy_pingpong=-80;
            }
            if(pingpongstate==6)
            {
                if(pointx.value>ballx.value-150 &&pointx.value<ballx.value+20 &&pointy.value<ball_vy.value+25 &&pointy.value>ball_vy.value-130)
                    {
                        vy_pingpong=-80;
                        vx_pingpong=30*keepPressL-30*keepPressR;
                        keepPressL=0;
                        keepPressR=0;
                        if(gametype==0)
                        {
                            
                            scores.value+=keep.value;
                            keep.value++;

                        }
                        pingpongstate=2;
                    }
            }

            
        }
        //enemybat rotate
        onMounted(()=>{
            setInterval(() => {
                rotatesz_ai.value=-60+120*(ai_x.value-75)/clientw;
                rotatesx_ai.value=-20;
                
            }, 20);
        })
        //enemybat move敌方拍子的ai控制
        onMounted(()=>{
            setInterval(() => {
                //只有当乒乓球已经开始被发出后的状态才进行跟踪反应
                if(pingpongstate==0)
                {
                    if(setplace==0)
                    {
                        ai_x.value=clientw/2-150;
                        ai_y.value=-25;
                        setplace=1;
                    }
                    else{
                        if(pointx.value<clientw/5 &&ai_x.value<clientw-pointx.value-400)
                        ai_x.value+=3
                        else if(pointx.value<1*clientw/2&&ai_x.value<clientw-pointx.value-400)
                        ai_x.value+=1
                        if(pointx.value>3*clientw/5 &&ai_x.value>clientw-pointx.value)
                        ai_x.value-=3
                        else if(pointx.value>clientw/2 &&ai_x.value>clientw-pointx.value)
                        ai_x.value-=1
                        
                    }
                    
                }
                else{
                    setplace=0;
                }
                if(pingpongstate==2)
                {
                    //如果没过半场，只进行x轴的变化，过了的话则可能往前
                    if(ball_vy.value>clienth/3)
                    {   //先判定目前的位置是不是沿着球的轨迹，如果沿着就不移动
                        
                        let dx=ballx.value+(bally.value-ai_y.value)*vx_pingpong/(-vy_pingpong);
                        // console.log(dx)
                        if(ai_x.value<dx+clientw/7&&ai_x.value>dx-clientw/7)
                        {
                            // alert('dont move')
                        }
                        else{
                            //否则通过三档速度方式定速，尽可能的逼近球最后会到达的位置
                            //但这样的话有些时候会很奇怪，例如如果x轴偏移速度很大，但y轴的速度不大的时候，出现的情况是
                            //最后目的点会和当前的位置差的很比较远，所以为了显得像真人玩家，会让它现在的x和预期在相差很大的时候
                            //跟着现在的位置走
                        if(Math.abs(dx-ballx.value)>0.5*clientw)
                        {
                            if(ai_x.value<ballx.value-200)
                        ai_x.value+=4;
                        else if(ai_x.value<ballx.value)
                        ai_x.value+=2.5;
                        if(ai_x.value>ballx.value+200)
                        ai_x.value-=4;
                        else if(ai_x.value>ballx.value)
                        ai_x.value-=2.5;
                        reached=false;
                        }
                        //不然向着既定目标地走
                        //如果已经到达既定位置后，一动不动也是有些奇怪的，所以加上点假动作
                        else{
                            if(!reached){
                                if(-ai_x.value+dx>0.16*clientw)
                                ai_x.value+=6;
                                else if(-ai_x.value+dx>0.1*clientw)
                                ai_x.value+=4;
                                else if(-ai_x.value+dx>0)
                                ai_x.value+=3;
                                if(ai_x.value-dx>0.16*clientw)
                                ai_x.value-=6;
                                else if(ai_x.value-dx>0.1*clientw)
                                ai_x.value-=4;
                                else if(ai_x.value-dx>0)
                                ai_x.value-=3;
                                if(ai_x.value<dx+30&&ai_x.value>dx-30)
                                {
                                    reached=true;
                                }
                            }
                            if(reached){
                                if(ai_x.value<ballx.value)
                                ai_x.value+=(ballx.value-ai_x.value)/30+1;
                                else
                                ai_x.value-=(-ballx.value+ai_x.value)/30+1;
                            }
                   
                        }
                        
                        }
                        
                    }
                    else{
                        
                        //     if(ai_x.value<ballx.value-200)
                        // ai_x.value+=4;
                        // else if(ai_x.value<ballx.value)
                        // ai_x.value+=2;
                        // if(ai_x.value>ballx.value+200)
                        // ai_x.value-=4;
                        // else if(ai_x.value>ballx.value)
                        // ai_x.value-=2;
                        let dx=ballx.value+(bally.value-ai_y.value)*vx_pingpong/(-vy_pingpong);
                        if(ai_x.value>ballx.value&&ai_x.value<dx)
                        ai_x.value-=1.5;
                        else if(ai_x.value>ballx.value)
                        ai_x.value-=(ai_x.value-ballx.value)/8+2;
                        if(ai_x.value<ballx.value&&ai_x.value>dx)
                        ai_x.value+=1.5;
                        else if(ai_x.value<ballx.value)
                        ai_x.value+=(-ai_x.value+ballx.value)/8+2;

                        if(ai_y.value<ball_vy.value&&ai_y.value+200>ball_vy.value &&ai_y.value<clienth/9-20 &&ball_vy.value>clienth/7)
                            ai_y.value+=2;
                         if( ai_x.value>ballx.value-45 && ai_x.value<ballx.value+45 &&( ai_y.value>bally.value-80&&ai_y.value<bally.value+80))
                        caught=1;
                        if(caught)
                        {   
                 
                        ai_x.value=ballx.value-60;
                        if(bally.value<clienth/4+50  &&ai_y.value<bally.value+90 &&ball_vy.value-ai_y.value<100)
                        {
                            caught=1;
                            if(hit==0)
                            {
                                hit=1;
                               setTimeout(() => {
                                hit=0;
                                ai_y.value=-25;
                                pingpongstate=5;
                                caught=0;
                            },  50); 
                            }
                        } 
                        }
                    }

                }
                if(pingpongstate==3)
                {
                    
                    //先检测是在谁的地盘出去的，如果是在我方半场出台面的，对面不需要去接着，不然得去接
                    //通常接不到，但是看着这种无能狂怒的感觉很有效果
                    if(drop_y<clienth/2)
                    {   
                        
                        if(ai_y.value<bally.value)
                        {
                            if(ai_x.value<ballx.value-10)
                                ai_x.value+13;
                        else if(ai_x.value>ballx.value+10)
                                ai_x.value-=13
                        }
                        
                         if(ai_x.value<ballx.value+20 &&ai_x.value>ballx.value-20 &&ai_y.value<bally.value &&ball_vy.value-ai_y.value<80)
                            {
                                
                            caught=1;
                            if(hit==0)
                            {
                                hit=1;
                               setTimeout(() => {
                                hit=0;
                                ai_y.value=-25;
                                pingpongstate=5;
                            }, 50); 
                            }
                        } 

                    }

                }
            }, 10);
        })
        let showhs=ref(false);
        function subitscore(){
            showhs.value=true;
            
            axios.get("http://8.130.81.131:8000/tennisq").then(res=>{
                let txtarea=document.getElementById("highscore");
                let info=res.data;
                let rank=0;
                for (const item of info) {
                    rank++;
                    txtarea.value+="第"+rank+"名：用户名"+item.name+" 成绩："+item.score+'\n';
                }
            })
        }
        
        function truesubmit(){
            let namex="nameless";
            let x=document.getElementById("scorename").value;
            if(x.length>0)
            namex=x;
            axios.get("http://8.130.81.131:8000/tennisi",{
                params: {
                    psw: "wcr",
                    name:namex,
                    score:scores.value
                }
            }).then(
            res=>{
                console.log(res)
            }
            )
            showhs.value=false;
            pingpongstate=0;
        }
        return{
            pointx,pointy,moves,clienth,clientw,rotatesz,rotatesx,ballx,bally,ballz,ball_vy,vx,vy,pingpongstate,fires,showpingpong,ai_x,ai_y,
            rotatesx_ai,rotatesz_ai,scores,keep,me_score,ai_score,s_bat,bounce,cheer,down,subitscore,showhs,truesubmit
        }
    }
    
    }

</script>

<style lang="css">
#gameconsole{
    position: relative;
        width:100vw;
        height: 100vh;
